import React, { Component } from 'react';
import constants from '../utils/constants';

export default class UserEntry extends Component {
  constructor(props) {
    super(props);
    const {
      interval, timespan, resourceWeights, itemWeights
    } = this.props;
    this.state = {
      interval,
      timespan,
      resourceWeights,
      itemWeights
    };
  }

  updateIntervalHours = (e) => {
    const targetValue = Number.parseInt(e.target.value, 10);
    this.setState(
      (state) => {
        return {
          interval: {
            hours: targetValue || 0,
            minutes: state.interval.minutes
          }
        };
      },
      () => {
        this.props.updateInterval(this.state.interval);
      }
    );
  };

  updateIntervalMinutes = (e) => {
    const targetValue = Number.parseInt(e.target.value, 10);
    this.setState(
      (state) => {
        return {
          interval: {
            hours: state.interval.hours,
            minutes: targetValue || 0
          }
        };
      },
      () => {
        this.props.updateInterval(this.state.interval);
      }
    );
  };

  updateTimespanHours = (e) => {
    const targetValue = Number.parseInt(e.target.value, 10);
    this.setState(
      (state) => {
        return {
          timespan: {
            hours: targetValue || 0,
            minutes: state.timespan.minutes
          }
        };
      },
      () => {
        this.props.updateTimespan(this.state.timespan);
      }
    );
  };

  updateTimespanMinutes = (e) => {
    const targetValue = Number.parseInt(e.target.value, 10);
    this.setState(
      (state) => {
        return {
          timespan: {
            hours: state.timespan.hours,
            minutes: targetValue || 0
          }
        };
      },
      () => {
        this.props.updateTimespan(this.state.timespan);
      }
    );
  };

  updateResourceWeightConstructor = (resource) => {
    return (e) => {
      const targetValue = Number.parseInt(e.target.value, 10);
      this.setState(
        (state) => {
          return {
            resourceWeights: Object.assign({}, state.resourceWeights, {
              [resource]: targetValue || 0
            })
          };
        },
        () => {
          this.props.updateResourceWeights(this.state.resourceWeights);
        }
      );
    };
  };

  updateTotalResourcesWeight = (e) => {
    const targetValue = Number.parseInt(e.target.value, 10);
    this.setState(
      (state) => {
        return {
          resourceWeights: Object.assign({}, state.resourceWeights, {
            totalResources: targetValue || 0
          })
        };
      },
      () => {
        this.props.updateResourceWeights(this.state.resourceWeights);
      }
    );
  };

  updateItemWeightConstructor = (item) => {
    return (e) => {
      const targetValue = Number.parseInt(e.target.value, 10);
      this.setState(
        (state) => {
          return {
            itemWeights: Object.assign({}, state.itemWeights, { [item]: targetValue || 0 })
          };
        },
        () => {
          this.props.updateItemWeights(this.state.itemWeights);
        }
      );
    };
  };

  render() {
    return (
      <div>
        <div>
          <div>
            Instructions:
            <br />
            Active Interval is how often you can/want to check on your echelons to start another
            mission.
            <br />
            Timespan is how long you can play for.
          </div>
          <br />
          <div>
            Example 1:
            <br />
            You have 4 hours where you can check on your echelons as soon as they are done with
            their previous mission.
            <br />
            Active Interval: 0 hours 0 minutes
            <br />
            Timespan: 4 hours 0 minutes
          </div>
          <br />
          <div>
            Example 2:
            <br />
            You plan to play the entire day (16 hours), checking on your echelons every half hour.
            <br />
            Active Interval: 0 hours 30 minutes
            <br />
            Timespan: 16 hours 0 minutes
          </div>
          <br />
          <div>
            Example 3:
            <br />
            You are going to sleep for 8 hours. You will not be checking on your echelons for the
            duration of sleep.
            <br />
            Active Interval: 8 hours 0 minutes
            <br />
            Timespan: 8 hours 0 minutes
          </div>
          <br />
          <div>
            Weights:
            <br />
            Quick <span style={{ textDecoration: 'line-through' }}>maths</span> ratios. If you set
            Manpower to 0, Ammo to 2, Rations to 0, and Parts to 1, it means you want ammo twice as
            much as you want parts and you do not care how much manpower or rations you get on the
            side. Same applies with item weights.
          </div>
          <br />
        </div>
        <table>
          <tbody>
            <tr>
              <th />
              <th>Hours</th>
              <th>Minutes</th>
            </tr>
            <tr>
              <td>Active Interval</td>
              <td>
                <input
                  type="number"
                  value={this.state.interval.hours}
                  onChange={this.updateIntervalHours}
                />
              </td>
              <td>
                <input
                  type="number"
                  value={this.state.interval.minutes}
                  onChange={this.updateIntervalMinutes}
                />
              </td>
            </tr>
            <tr>
              <td>Timespan</td>
              <td>
                <input
                  type="number"
                  value={this.state.timespan.hours}
                  onChange={this.updateTimespanHours}
                />
              </td>
              <td>
                <input
                  type="number"
                  value={this.state.timespan.minutes}
                  onChange={this.updateTimespanMinutes}
                />
              </td>
            </tr>
          </tbody>
        </table>
        <table>
          <tbody>
            <tr>
              <th />
              <th>Manpower</th>
              <th>Ammo</th>
              <th>Rations</th>
              <th>Parts</th>
              <th>Total Resources</th>
            </tr>
            <tr>
              <td>Weights</td>
              <td>
                <input
                  type="number"
                  value={this.state.resourceWeights[constants.RESOURCES.MANPOWER]}
                  onChange={this.updateResourceWeightConstructor(constants.RESOURCES.MANPOWER)}
                />
              </td>
              <td>
                <input
                  type="number"
                  value={this.state.resourceWeights[constants.RESOURCES.AMMO]}
                  onChange={this.updateResourceWeightConstructor(constants.RESOURCES.AMMO)}
                />
              </td>
              <td>
                <input
                  type="number"
                  value={this.state.resourceWeights[constants.RESOURCES.RATIONS]}
                  onChange={this.updateResourceWeightConstructor(constants.RESOURCES.RATIONS)}
                />
              </td>
              <td>
                <input
                  type="number"
                  value={this.state.resourceWeights[constants.RESOURCES.PARTS]}
                  onChange={this.updateResourceWeightConstructor(constants.RESOURCES.PARTS)}
                />
              </td>
              <td>
                <input
                  type="number"
                  value={this.state.resourceWeights.totalResources}
                  onChange={this.updateTotalResourcesWeight}
                />
              </td>
            </tr>
          </tbody>
        </table>
        <table>
          <tbody>
            <tr>
              <th />
              <th>T-Doll Contracts</th>
              <th>Equipment Contracts</th>
              <th>Quick Production</th>
              <th>Quick Repair</th>
              <th>Tokens</th>
            </tr>
            <tr>
              <td>Weights</td>
              <td>
                <input
                  type="number"
                  value={this.state.itemWeights[constants.ITEMS.T_DOLL_CONTRACT]}
                  onChange={this.updateItemWeightConstructor(constants.ITEMS.T_DOLL_CONTRACT)}
                />
              </td>
              <td>
                <input
                  type="number"
                  value={this.state.itemWeights[constants.ITEMS.EQUIP_CONTRACT]}
                  onChange={this.updateItemWeightConstructor(constants.ITEMS.EQUIP_CONTRACT)}
                />
              </td>
              <td>
                <input
                  type="number"
                  value={this.state.itemWeights[constants.ITEMS.QUICK_PRODUCTION]}
                  onChange={this.updateItemWeightConstructor(constants.ITEMS.QUICK_PRODUCTION)}
                />
              </td>
              <td>
                <input
                  type="number"
                  value={this.state.itemWeights[constants.ITEMS.QUICK_REPAIR]}
                  onChange={this.updateItemWeightConstructor(constants.ITEMS.QUICK_REPAIR)}
                />
              </td>
              <td>
                <input
                  type="number"
                  value={this.state.itemWeights[constants.ITEMS.TOKEN]}
                  onChange={this.updateItemWeightConstructor(constants.ITEMS.TOKEN)}
                />
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }
}



