import React, { Component } from 'react';
import { Helmet } from 'react-helmet';
import helpers from './utils/helpers';
import constants from './utils/constants';
import ParamsEntry from './components/ParamsEntry';
import Logistic from './components/Logistic';
import sorters from './utils/sorters';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      interval: {
        hours: 0,
        minutes: 0
      },
      timespan: {
        hours: 0,
        minutes: 0
      },
      resourceWeights: {
        [constants.RESOURCES.MANPOWER]: 0,
        [constants.RESOURCES.AMMO]: 0,
        [constants.RESOURCES.RATIONS]: 0,
        [constants.RESOURCES.PARTS]: 0,
        totalResources: 1
      },
      itemWeights: {
        [constants.ITEMS.T_DOLL_CONTRACT]: 0,
        [constants.ITEMS.EQUIP_CONTRACT]: 0,
        [constants.ITEMS.QUICK_PRODUCTION]: 0,
        [constants.ITEMS.QUICK_REPAIR]: 0,
        [constants.ITEMS.TOKEN]: 0
      }
    };
  }

  updateInterval = (interval) => {
    this.setState({
      interval
    });
  };

  updateTimespan = (timespan) => {
    this.setState({
      timespan
    });
  };

  updateResourceWeights = (resourceWeights) => {
    this.setState({
      resourceWeights
    });
  };

  updateItemWeights = (itemWeights) => {
    this.setState({
      itemWeights
    });
  };

  render() {
    const logisticsArray = [];
    helpers
      .getUnsortedArrayOfLogisticsEfficiencies(
        constants.LOGISTICS,
        this.state.interval,
        this.state.timespan
      )
      .sort(
        sorters.combineComparators([
          sorters.itemsBasedComparatorFactory(this.state.itemWeights),
          sorters.resourcesBasedComparatorFactory(this.state.resourceWeights),
          sorters.logisticNameComparator
        ])
      )
      .forEach((logistic) => {
        logisticsArray.push(
          <Logistic key={`${logistic.chapter}-${logistic.mission}`} {...logistic} />
        );
      });
    return (
      <div className="App">
        <Helmet>
          <script async src="//pagead2.googlesyndication.com/pagead/js/adsbygoogle.js" />
          <script>
            {`
            (adsbygoogle = window.adsbygoogle || []).push({
              google_ad_client: "ca-pub-7592334054841641",
              enable_page_level_ads: true
            });
            `}
          </script>
        </Helmet>
        <ParamsEntry
          {...this.state}
          updateInterval={this.updateInterval}
          updateTimespan={this.updateTimespan}
          updateResourceWeights={this.updateResourceWeights}
          updateItemWeights={this.updateItemWeights}
        />
        <table>
          <tbody>
            <tr>
              <th>Logistic</th>
              <th>Duration</th>
              <th>Occurrences</th>
              <th>Manpower</th>
              <th>Ammo</th>
              <th>Rations</th>
              <th>Parts</th>
              <th>Total</th>
              <th>Items</th>
            </tr>
            {logisticsArray}
          </tbody>
        </table>
      </div>
    );
  }
}

export default App;



